import React from "react"
import ReactGoogleMapLoader from "react-google-maps-loader"
import ReactGooglePlacesSuggest from "react-google-places-suggest"
import {Link} from "react-router-dom"
import "./script/city-list.css"


export default class GoogleSuggest extends React.Component {
    state = {
        search: "",
        value: "",
    }
    handleInputChange = e => {
        this.setState({search: e.target.value, value: e.target.value})
    }
    handleSelectSuggest = (geocodedPrediction, originalPrediction) => {
        // console.log(geocodedPrediction.formatted_address, 'GEOCODE') // eslint-disable-line
        this.setState({
            search: "",
            value: geocodedPrediction.formatted_address,
        })
    }
    handleNoResult = () => {
        // console.log("No results for ", this.state.search)
    }
    handleStatusUpdate = status => {
        // console.log(status)
    }
    render() {
        const {search, value} = this.state
        const valueSearch = value.split(',')
        const valueCity = valueSearch[0].toLowerCase().split(' ').join('_').normalize("NFD").replace(/[\u0300-\u036f]/g, '')
        const valueCountry = valueSearch[valueSearch.length-1].toLowerCase().split(' ').join('_').normalize("NFD").replace(/[\u0300-\u036f]/g, '').split('').slice(1).join('')
        
        return (
            <ReactGoogleMapLoader
                params={{
                    key: process.env.REACT_APP_GooglePlacesKey,
                    libraries: "places,geocode",
                }}
                render={googleMaps =>
                    googleMaps && (
                        <ReactGooglePlacesSuggest
                            googleMaps={googleMaps}
                            autocompletionRequest={{
                                input: search,
                                // Optional options
                                // https://developers.google.com/maps/documentation/javascript/reference?hl=fr#AutocompletionRequest
                            }}
                            // Optional props
                            onNoResult={this.handleNoResult}
                            onSelectSuggest={this.handleSelectSuggest}
                            onStatusUpdate={this.handleStatusUpdate}
                            textNoResults="My custom no results text" // null or "" if you want to disable the no results item
                            customRender={prediction => (
                                <div className="customWrapper">
                                    {prediction
                                        ? prediction.description
                                        : "My custom no results text"}
                                </div>
                            )}
                        >
                            <div className="searchSpace">
                            <input
                                className="search-input"
                                type="text"
                                name="search"
                                value={value}
                                placeholder="Buscar cidades"
                                onChange={this.handleInputChange}
                            />
                                <div className="search">
                                <Link to={`/city?value=${valueCity}+${valueCountry}`} className='searchicon'>  
                                        <i className="fas fa-search" style={{marginLeft: '0'}}></i>
                                </Link>
                                </div> 
                            </div>
                        </ReactGooglePlacesSuggest>
                    )
                }
            />
        )
    }
}
