import React, { Component } from "react";
import axios from "axios";
import "./profile.css"
import Navbar from "./navbar"

class City extends Component {
  constructor(props) {
    super(props);

    this.state = {
      itinerate: [],
      user: "",
      active: false,
      name: "",
      city: "",
      rating: "",
      address: "",
      id: "",
    };

    this.getItinerateHandler = this.getItinerateHandler.bind(this);
    this.deleteHandler = this.deleteHandler.bind(this);
    this.toggleClass = this.toggleClass.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleFormSubmit = this.handleFormSubmit.bind(this);
    // this.showAddTaskForm = this.showAddTaskForm.bind(this);
  }

  toggleClass(id) {
    // console.log("clicando");
    this.state.itinerate.map((elem, idx) => {
      // console.log(elem._id, 'elem', id, 'id')
      return elem._id === id ? 
    this.setState({active: !this.state.active}):
    elem
    });
  }

  getItinerateHandler() {
    axios
      .get(`${process.env.REACT_APP_API_URL}/itinerate`, { withCredentials: true })
      .then((responseFromApi) => {
        this.setState({
          itinerate: responseFromApi.data,
        });
      })
      // .catch((error) => console.log(error));
  }

  deleteHandler(idx) {
    const { itinerate } = this.state;
    const newArr = [...itinerate];
    const newItinerate = [];
    // console.log(newItinerate, "delete");
    // console.log(idx, "idx");

    newArr.splice(idx, 1);
    
    itinerate.map((elem, index) => {
      if (index === idx) {
        newItinerate.push(elem);
      }
    });
    axios
      .post(`${process.env.REACT_APP_API_URL}/itinerate`, { newItinerate })
      .then(() => {
        this.setState({ itinerate: newArr });
      })
      // .catch((error) => console.log(error));
  }

  componentDidMount() {
    this.getItinerateHandler();
  }

  componentWillMount() {
    this.state.itinerate.map((elem) => {
      this.state.name = elem.name;
      this.state.rating = elem.rating;
      this.state.city = elem.city;
      this.state.adress = elem.formatted_address;
      // this.state.id = elem._id
    });
    this.setState(this.state);
  }


  handleFormSubmit(event) {
    const name = this.state.name;
    const city = this.state.city;
    const rating = this.state.rating;
    const address = this.state.formatted_address;
    const user = this.props.loggedInUser._id;
    const id = this.state.itinerate.map((item) => {
      return item._id;
    });
    event.preventDefault();

    axios
      .put(
        `${process.env.REACT_APP_API_URL}/itinerate`,
        { name, city, rating, address, user, id },
        { withCredentials: true }
      )
      .then(() => {
        // this.getItinerateHandler();
        this.props.history.push(`/profile/${user}`);
      })
      // .catch((error) => console.log(error));
  }

  handleChange(event) {
    const { name, value } = event.target;
    // console.log(name, value, "valor");
    this.setState({ [name]: value });
  }

  render() {
    // console.log(this.props.loggedInUser.name, 'user')
    return (
      <div className="apagar">
        <div className="logo">
          <img src="/image/logo.png" className="meuRoteiro-icon"/>
        </div>
        <div className="cities-header">
            <div className="cities-clouds"></div>
            <Navbar isLogged={this.props.isLogged} userInSession={this.props.loggedInUser}/>
        </div>
        <div className="profile-body">
          <h1>{this.props.loggedInUser.name}</h1>
          <i className="fas fa-sticky-note script-icon"></i>
        <div className="roteiro">
          {this.state.itinerate
            .filter((name) => name.user === this.props.loggedInUser._id).sort((a, b) => a.city.localeCompare(b.city))
            .map((elem, idx) => {
              return (
                <div key={elem._id} className="roteiro-info">
                  <div className="margin">
                    <div className="roteiro-details">
                      <p className="placeName">{elem.city}</p>
                      <p className="placeCity">{elem.name}</p>
                      <p className="placeSub">Endereço: {elem.formatted_address}</p>
                      <p className="placeSub">Rating: {elem.rating}</p>
                    </div>
                    <div className="roteiro-btns">
                      {/* <button onClick={() => this.toggleClass(elem._id)} className="roteiro-btn">
                        <i className="fas fa-pencil-alt roteiro-icon"></i>
                      </button> */}
                      <button onClick={() => this.deleteHandler(idx)} className="roteiro-btn">
                        <i className="fas fa-trash-alt roteiro-icon"></i>
                      </button>
                    </div>
                    <div>
                      <p className="linhaRoteiro">_____________________</p>
                    </div>
                  </div>

                  {this.state.active && 
                  <form onSubmit={this.handleFormSubmit}>
                    <input
                      type="text"
                      name="city"
                      placeholder={elem.city}
                      value={this.state.city}
                      onChange={this.handleChange}
                    />

                    <input
                      type="text"
                      name="name"
                      placeholder={elem.name}
                      value={this.state.name}
                      onChange={this.handleChange}
                    />

                    <input
                      type="text"
                      name="formatted_address"
                      placeholder={elem.formatted_address}
                      value={this.state.formatted_address}
                      onChange={this.handleChange}
                    />

                    {/* <input
                      type="number"
                      name="rating"
                      placeholder={elem.rating}
                      value={this.state.rating}
                      onChange={this.handleChange}
                    /> */}

                    <p name="id" value={elem._id}></p>

                    <input className="button" type="submit" value="Submit" />
                  </form>
                } 

                </div>
              );
            })}
            </div>
          {/* {this.showAddTaskForm()} */}
        </div>
      </div>
    );
  }
}

export default City;
