import React, {Component} from 'react';
import './App.css';
import Signup from './components/login/signup';
import Login from './components/login/login';
import {Route, Switch} from 'react-router-dom'
import AuthService from './components/auth-service'
import ScriptService from './components/script-service'
import CityList from './components/script/city-list';
import City from './components/script/city-details';
import CityAxios from "./components/script/city"
import Profile from "./components/profile"
import axios from "axios"
import ProtectedRoute from "./components/protected-routes";
import Navbar from "./components/navbar"
import Homepage from "./Views/HomePage/Homepage"
import 'bootstrap/dist/css/bootstrap.min.css';



class App extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      loggedInUser: null,
      museums: [],
      restaurants: [],
      enterntainment: [],
      tourist: [],
      aquarium: [],
      bakery: [],
      cafe: [],
      movie_theater: [],
      zoo: [],
      park: [],
      library: [],
      newScript: [],
      newNameScript: []
      };

    this.service = new AuthService();
    this.script = new ScriptService();
    this.getTheUser = this.getTheUser.bind(this);
    this.handleLogout = this.handleLogout.bind(this);
    this.getCity = this.getCity.bind(this)
    this.myScript = this.myScript.bind(this)
  }


  getCity() {
    axios
    .get(`${process.env.REACT_APP_API_URL}/city`, { withCredentials: true })
    .then((responseFromApi ) => {
      this.setState({
        restaurants: responseFromApi.data.restaurantUnits.results
      })
      this.setState({
        museums: responseFromApi.data.museumUnits.results,
      })
      this.setState({
        enterntainment: responseFromApi.data.enterUnits.results,
      })
      this.setState({
        tourist: responseFromApi.data.touristUnits.results,
      })
      this.setState({
        aquarium: responseFromApi.data.aquariumUnits.results,
      })
      this.setState({
        bakery: responseFromApi.data.bakeryUnits.results,
      })
      this.setState({
        cafe: responseFromApi.data.cafeUnits.results,
      })
      this.setState({
        movie_theater: responseFromApi.data.movie_theaterUnits.results,
      })
      this.setState({
        zoo: responseFromApi.data.zooUnits.results,
      })
      this.setState({
        park: responseFromApi.data.parkUnits.results,
      })
      this.setState({
        library: responseFromApi.data.libraryUnits.results,
      })
    })
    .catch(error => (console.log(error)))
    
}

  fetchUser() {
    // console.log("inside fetchUser");
    if (this.state.loggedInUser === null) {
      // console.log("NULL inside fetchUser");
      this.service
        .loggedin()
        .then((response) => {
          // console.log("SUCCESS inside fetchUser");
          this.setState({
            loggedInUser: response,
          });
        })
        .catch((err) => {
          // console.log("ERROR inside fetchUser");
          this.setState({
            loggedInUser: false,
          });
        });
    }
  }


  getTheUser(userObj) {
    this.setState({
      loggedInUser: userObj,
    });
  }


  myScript(newName) {
    // const myOwnScript = [...this.state.newScript]
    // myOwnScript.push(script)
   // this.setState({ newScript: myOwnScript})
    const myOwnScript = []
    myOwnScript.push(newName)
    this.setState({ newNameScript: myOwnScript})
    
  }



  handleLogout() {
    this.service
      .logout()
      .then(() => {
        console.log(this.history)
        this.setState({
          loggedInUser: null,
        });
        // this.props.history.push("/login");
      })
      .catch();
  }

  componentDidMount() {
    // this.myScript()
  }

  render(){
    console.log(this.state.loggedInUser, 'user')
    this.fetchUser();
    if (this.state.loggedInUser) {
  return (
    <div className="App">
      {/* <Navbar userInSession={this.state.loggedInUser} getUser={this.getTheUser}/> */}
      <Switch>
        <Route exact path='/' component={() => <Homepage isLogged={true} userInSession={this.state.loggedInUser} getUser={this.props.getTheUser} />}  />
        <Route exact path='/cities'  render={(props) => <CityList isLogged={true} user={this.state.loggedInUser} {...props}/>}/>
        <Route exact path='/city'render={(props) => <CityAxios isLogged={true} getCity={this.getCity} getTheUser={this.getTheUser} myScript={this.myScript}
        museums={this.state.museums} 
        enterntainment={this.state.enterntainment} 
        restaurants={this.state.restaurants} 
        tourist={this.state.tourist} 
        aquarium={this.state.aquarium} 
        bakery={this.state.bakery} 
        cafe={this.state.cafe} 
        movie_theater={this.state.movie_theater} 
        zoo={this.state.zoo} 
        park={this.state.park} 
        library={this.state.library} 
        newScript={this.state.newScript}
        newNameScript={this.state.newNameScript}
        user={this.state.loggedInUser}
        {...props}
        />}/>


<ProtectedRoute user={this.state.loggedInUser} newNameScript={this.state.newNameScript} getCity={this.getCity} 
        museums={this.state.museums} 
        enterntainment={this.state.enterntainment} 
        restaurants={this.state.restaurants} 
        tourist={this.state.tourist} 
        aquarium={this.state.aquarium} 
        bakery={this.state.bakery} 
        cafe={this.state.cafe} 
        movie_theater={this.state.movie_theater} 
        zoo={this.state.zoo} 
        park={this.state.park} 
        library={this.state.library} 
       exact path='/profile/:id' component={Profile}/>
      </Switch>
      </div>
  );
    } else {
      return (
        <div>
        <Switch>
          <Route exact path='/' component={() => <Homepage isLogged={false} />} />
          <Route exact path='/login' render={(props) => <Login getTheUser={this.getTheUser} {...props}/>}/>
          <Route exact path='/signup' render={(props) => <Signup getTheUser={this.getTheUser} {...props}/>}/>
          <Route exact path='/cities' render={(props) => <CityList {...props} user={this.state.loggedInUser} isLogged={false}/>}/>
        <Route exact path='/city' render={(props) => <CityAxios isLogged={false} getCity={this.getCity}  myScript={this.myScript} getTheUser={this.getTheUser}
        museums={this.state.museums} 
        enterntainment={this.state.enterntainment} 
        restaurants={this.state.restaurants} 
        tourist={this.state.tourist} 
        aquarium={this.state.aquarium} 
        bakery={this.state.bakery} 
        cafe={this.state.cafe} 
        movie_theater={this.state.movie_theater} 
        zoo={this.state.zoo} 
        park={this.state.park} 
        library={this.state.library} 
        newScript={this.state.newScript}
        newNameScript={this.state.newNameScript}
        user={this.state.loggedInUser}
        {...props}
        />}/>

        </Switch>
        
        </div>
  
  );
}
}
}

export default App;
