import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import AuthService from "./auth-service";
import "../App.css"

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = { loggedInUser: this.props.userInSession, isLogged: this.props.isLogged}
    this.service = new AuthService();
    this.logoutUser = this.logoutUser.bind(this);
  }

  componentDidUpdate(prevProps) {
    if (this.props.userInSession !== prevProps.userInSession) {
      this.setState({ isLogged: this.props.userInSession });
    }
  }

  logoutUser() {
    this.service.logout().then(() => {
      this.setState({ loggedInUser: null });
      this.props.getTheUser(null);
    });
  }

  render() {
    if (this.state.loggedInUser) {
      // console.log(this.state.loggedInUser, 'user loggedin')
      return (
        <nav>
          <div className="navbar-menu">
            <div className="navbar-start">
              <NavLink to="/" className="link">
                Página inicial
              </NavLink>
              <NavLink to={"/profile/" + this.state.loggedInUser._id}  className="link">
                Meus roteiros
              </NavLink>
              <NavLink
                className="navbar-item link"
                activeClassName="is-active"
                to="/cities"
              >
                Encontre uma cidade
              </NavLink>
              <button className="logout-btn" onClick={this.logoutUser}>
                Sair
              </button>
            </div>
          </div>
        </nav>
      );
    } else {
      // console.log("nao logado")
      return (
        <nav>
          <div className="navbar-menu">
            <div className="navbar-start">
            <NavLink
                className="navbar-item link"
                activeClassName="is-active"
                to="/cities"
              >
                Encontre uma cidade
              </NavLink>
              <NavLink to="/login" className="link">Login</NavLink>
              <NavLink
                className="navbar-item link borda"
                activeClassName="is-active"
                to="/signup"
              >
                Cadastre-se
              </NavLink>
            </div>
          </div>
        </nav>
      );
    }
  }
}

export default Navbar;