import React, { Component } from 'react'
import axios from 'axios' 

class City extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             city: null,
        }
        this.getSingleCity = this.getSingleCity.bind(this);
    }
    
    getSingleCity() {
        // console.log(this.props);
        const { id } = this.props.match.params;
    
        axios
          .get(`${process.env.REACT_APP_API_URL}/cities/` + id, {
            withCredentials: true,
          })
          .then((response) => {
            // console.log(response, " RESPONSEEEEEE")
           this.setState({city: response.data.results[0] })})
          .catch((error) => console.log(error));
      }
    
      componentDidMount() {
        this.getSingleCity();
      }


    render() {
    // console.log(this.state.city, 'state city')
        return (
            <div>
              {this.state.city && this.state.city.name}
            </div>
        )
    }
}


export default City;