import React from "react";
import { Route, Redirect } from "react-router-dom";

const ProtectedRoute = ({ component: Component, user, getCity, newNameScript, ...rest }) => {
  return (
    
    <Route exact path='/profile/:id'
    
      {...rest}
      
      render={(props) => {
        if (user) {
          return <Component {...props}  loggedInUser={user} newNameScript={newNameScript} getCity={getCity}/>;
        } else {
          return (
            <Redirect to={{ pathname: "/", state: { from: props.location } }} />
          );
        }
      }}
    />
  );
};
export default ProtectedRoute;