import axios from 'axios'

class ScriptService {
    constructor() {
      let script = axios.create({
        baseURL: `${process.env.REACT_APP_API_URL}`,
        withCredentials: true
      });
      this.script = script;
    }
    cities(name, image) {
        
        return this.script.get('/cities', {name, image})
        .then(response => response.data)
    }

    city(name, photos) {
      // console.log('places api')
      return this.script.get('/city', {name, photos})
      .then(response => response.data)
  }

    
}

export default ScriptService;