import React, { Component } from 'react'
import AuthService from '../auth-service';
import {Link} from "react-router-dom";

export default class Signup extends Component {
    constructor(props) {
        super(props);
        this.state = { username: "", password: "", name: "", email: ""};
        this.service = new AuthService();
        this.handleFormSubmit = this.handleFormSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
      }
      handleFormSubmit(event) {
        event.preventDefault();
        const username = this.state.username;
        const password = this.state.password;
        const name = this.state.name;
        const email = this.state.email;
        this.service
          .signup(username, password, name, email)
          .then((response) => {
            this.setState({
              username: "",
              password: "",
              name: "",
              email: ""
            });
            this.props.getTheUser(response)
            this.props.history.push("/");
          })
          .catch((error) => console.log(error));
      }
      handleChange(event) {
        const { name, value } = event.target;
        this.setState({ [name]: value });
      }
    render() {
        return (
            <div className="login">
              <div className="form">
                <h1>Cadastre-se</h1>
                <form onSubmit={this.handleFormSubmit}>
                    <input className="input" type='text' name='username' placeholder='Username' onChange={this.handleChange}></input>
                    <br/>
                    <input className="input" type='password' name='password' placeholder='Senha' onChange={this.handleChange}></input>
                    <br/>
                    <input className="input" type='text' name='name' placeholder='Nome' onChange={this.handleChange}></input>
                    <br/>
                    <input className="input" type='text' name='email' placeholder='Email' onChange={this.handleChange}></input>
                    <br/> 
                    <button className="button">Enviar</button>
                    <br/>
                    <span className="span">Já possui uma conta?</span><Link to="/login" className="entrar"> Entre</Link>
                </form>
              </div>
                <div className="image"></div>
            </div>
        )
    }
}